.youtube {
    .inner {
        .frame {
            &::after {content: ""; display: block; clear: both;}

            article {
                width: 20%;
                height: 500px;
                float: left;
                padding: 10px;
                overflow: hidden;

                .inner {
                    width: 100%;
                    height: 100%;
                    background-color: #fff;
                    box-shadow: 5px 5px 15px rgba(0,0,0,0.1);
                    padding: 10px;
                    display: flex;
                    flex-wrap: wrap;
                    align-content: space-between;

                    /* 
                    word-break : 텍스트의 줄바꿈 속성을 지정
                    normal : 한글은 한글자 기준, 영어는 단어기준으로 줄바꿈이된다.
                    break-all : 한글 영어 모두 알파벳 기준으로 줄바꿈
                    keep-all : 한글 영어 모두 단어 기준으로 줄바꿈
                    */

                    .txt {
                        h2 {
                            font: bold 18px/1.2 "arial";
                            color: #555;
                            margin-bottom: 20px;
                            word-break: break-all;
                        }

                        p {
                            font: 12px/1.2 "arial";
                            color: #888;
                            word-break: break-all;
                        }
                    }

                    .pic {
                        width: 100%;
                        height: 150px;
                        cursor: pointer;

                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }
                }
            }
        }

        .pop {
            width: 100%;
            height: 100vh;
            position: fixed;
            top: 0px;
            left: 0px;
            background: rgba(0,0,0,0.9);
            padding: 5vw;

            iframe {
                border: none;
                width: 100%;
                height: 100%;
            }

            span {
                position: absolute;
                top: 3vw;
                right: 5vw;
                font: bold 14px/1 "arial";
                color: #fff;
                cursor: pointer;
            }
        }
    }
}